import { useEffect, useState } from "react";
import PersonForm from "./PersonForm";
import Persons from "./Persons";
import Filter from "./Filter";
import services from "../services/person";
import Notification from "./Notification";
import "../index.css";

const App = () => {
  const [persons, setPersons] = useState([]);
  const [newName, setNewName] = useState("");
  const [newNumber, setNewNumber] = useState("");
  const [notif, setNotif] = useState(null);

  const handleChange = (event) => {
    setNewName(event.target.value);
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    const person = {
      name: newName,
      number: newNumber,
    };

    if (checkIfNameExists(persons, newName)) {
      const addNewOrNot = window.confirm(
        `${newName} added to the phonebook, replace it with new number?`
      );

      if (addNewOrNot) {
        modifyPerson(person, newName);
        setNotif(`${person.name} modified`);
        showNotif();
      }
    } else {
      addNumber(person);
      setNotif(`${person.name} added successfully`);
      setTimeout(() => {
        setNotif(null);
      }, 5000);
    }
  };

  const addNumber = (person) => {
    services.addPerson(person).then((returnedPerson) => {
      setPersons(persons.concat(returnedPerson));
      setNewName("");
      setNewNumber("");
    });
  };

  const modifyPerson = (person, name) => {
    const personToModify = persons.find((person) => person.name === name);
    const changedContact = { ...personToModify, number: person.number };
    services.updatePerson(changedContact).then((changedContact) => {
      setPersons(
        persons.map((person) =>
          person.name === changedContact.name ? changedContact : person
        )
      );
    });
  };

  const checkIfNameExists = (persons, name) => {
    let i = 0;
    while (i !== persons.length) {
      // console.log(persons[i].name, name);
      if (persons[i].name === name) return true;
      i++;
    }
    return false;
  };

  const handleChangeInNumber = (event) => {
    setNewNumber(event.target.value);
  };

  const showFiltered = (event) => {
    setPersons(
      persons.filter((person) =>
        person.name.toLowerCase().includes(event.target.value)
      )
    );
  };

  const hook = () => {
    services
      .getAllNumbers()
      .then((initialPersons) => setPersons(initialPersons));
  };

  const showNotif = () => {
    setTimeout(() => {
      setNotif(null);
    }, 5000);
  };

  useEffect(hook, []);

  return (
    <div>
      <h2>Phonebook</h2>
      <Notification msg={notif} />
      <Filter showFiltered={showFiltered} />
      <h2>add a new</h2>
      <PersonForm
        handleChange={handleChange}
        handleChangeInNumber={handleChangeInNumber}
        handleSubmit={handleSubmit}
        newName={newName}
        newNumber={newNumber}
      />
      <h2>Number</h2>
      <Persons persons={persons} setPersons={setPersons} />
    </div>
  );
};

export default App;
